<template>
    <div style="position: absolute;bottom: 207px; width: 464px; background: #fff;">
        <div class="emotion-box" :style="{height: height + 'px' }" >
            <div class="emotion-box-line" v-for="(line, i) in list" :key="i" >
                <emotion class="emotion-item" v-for="(item, i) in line" :key="i" @click.native="clickHandler(item)" >{{item}}</emotion>
            </div>
        </div>
    </div>
</template>

<script>
    import Emotion from './Emotion'
    export default {
        props: {
            height: {
                type: Number,
                default: 200,
            }
        },
        data () {
            return {
                list: [
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', '',],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                    ['NO', 'OK', '', '', '', '', '', ''],
                    ['', '', '', '', '', '', '', ''],
                ]
            }
        },
        methods: {
            clickHandler (i) {
                let emotion = `#${i};`
                this.$emit('emotion', emotion)
            }
        },
        components: {
            Emotion
        }
    }
</script>
<style scoped>
    .emotion-box {
        margin: 0 auto;
        width: 100%;
        box-sizing: border-box;
        padding: 3px;
        border: 1px solid rgba(226, 226, 226, 0.84);
        border-radius: 5px;
        overflow: hidden;
        overflow-y: auto;
        position: initial;
        z-index:2000
    }
    .emotion-box-line {
        display: flex;
    }
    .emotion-item {
        flex: 1;
        text-align: center;
        cursor: pointer;
    }
</style>



