<template>
    <div class="ly-emotion" >
        <slot></slot>
    </div>
</template>
<script>
    export default {
        name: 'Emotion',
        mounted () {
            const name = this.$el.innerHTML
            const list = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 'NO', 'OK', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
            let index = list.indexOf(name)
            let imgHTML = `<img src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif" alt="[${name}]">`
            this.$nextTick(() => {
                this.$el.innerHTML = imgHTML
            })
        },
    }
</script>
<style scoped>
    .ly-emotion {
        display: inline-block
    }
</style>



